body { margin:0; padding:0; font-family: 'Poppins', sans-serif; background: url(/r/arkaplan.jpg) center center no-repeat; background-size:auto!important; }

input:-internal-autofill-selected {
    background: red!important;
}

#root {
    width:100vw;
    height:100vh;
    display:flex;
    justify-content: center;
    align-items: center;
    background: rgba(255, 255, 255, 0.55);
}

.container {
    max-width: 90vw;
    max-height:90vh;
    margin:0 auto;
    background: #fff;
    padding: 4px;
    background: rgba(255, 255, 255, 0.85)!important;
    backdrop-filter: blur(5px);
    -webkit-backdrop-filter: blur(5px);
    overflow: hidden!important;
}

.baslik {
    width:calc(calc(100% + 8px) - 16px);
    padding:0 8px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    background:linear-gradient(to bottom, #f4f4f4, transparent);
    position: relative;
    top:-4px;
    left:-4px;
}

.baslik h3 {
    font-size:18px;
    font-weight: bold;
    color:#223394;
    user-select: none;
}

.routeContainer {
    width:100%;
    max-width: 90vw;
    height:100%;
    max-height:90vh;
    display: flex;
    flex-direction: column
}

.girisYap form {
    width:calc(100% - 8px);
    display:flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 4px;
}
.girisInput {
    width:100%;
    margin-bottom:15px!important;
}

.girisButon {
    width:100%;
}

.girisUyari {
    width:100%;
    font-size:13px;
    font-weight: bold;
    color:red;
    text-align: center;
    margin:0 0 15px;
}

.siparisEkle {
    width:calc(100% - 8px);
    display:flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    padding: 4px;
}
.siparisEkleUyari {
    font-size:14px;

    margin:0;
    padding:2px 0;
}
.siparisEkle form {
    width:calc(100%);
}

.siparisEkle form tr {
    width:calc(100%);
    display:flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
    margin-bottom: 15px;
}

.siparisListe {
    flex:1;
    overflow-y: scroll;
    overflow-x: hidden;
    scrollbar-width:thin;
    margin-bottom:35px;
}
.siparisListeHead tr th { height: 48px; background: linear-gradient(to bottom, #fff, #fafafade); backdrop-filter: blur(5px); -webkit-backdrop-filter: blur(5px); font-weight: bold; font-size: 14px; color:#555; }

.siparisListe tbody tr { transition: all .3s linear; }
.siparisListe tbody tr:hover { background:#f4f4f4; }

.siparisListe .adSoyad { min-width: 130px; }
.siparisListe .kargoAdresi { min-width: 150px; }
.siparisListe .eklenmeTarihi { min-width: 105px; }

.siparisListeFooter {
    position: fixed;
    width:calc(100% - 8px);
    height: 35px;
    padding: 2.5px 0;
    background: #fff;
    bottom:0;
    background: linear-gradient(to bottom, #fff, #fafafade);
    backdrop-filter: blur(5px);
    -webkit-backdrop-filter: blur(5px);
    border-top:1px solid #f4f4f4;
}
.tumSiparisleriGoster {
    margin-left: 15px;
    text-decoration: none;
    line-height: 35px;
    font-size:13px;
    font-weight: bold;
}
.siparisListeToplamlar {
    line-height:35px;
    padding: 0 15px;
    font-size:13px;
    font-weight: bold;
    float:right;
    color:#555;
}
.siparisListeToplamlar b {
    color:#000;
}

.siparisBulunamadÄ± {
    width:100%;
    display: block;
    text-align: center;
    font-size:13px;
}

.siparisSilModalContainer {
    position: fixed;
    display: flex;
    justify-content: center;
    align-items: center;
    width:calc(100% - 8px);
    height:calc(100% - 8px);
    background: rgba(255, 255, 255, 0.58);
    z-index: 99999;
}

.siparisSilModal {
    display: block;
    box-shadow: 0px 2px 1px -1px rgba(0,0,0,0.2), 0px 1px 1px 0px rgba(0,0,0,0.14), 0px 1px 3px 0px rgba(0,0,0,0.12);
    border-radius: 4px;
    background: #fff;
}
.siparisSilModal h3 {
    width:calc(100% - 30px); padding: 20px 15px; margin:0; font-size:15px; color:#555; background: linear-gradient(to bottom, #fafafade, #fff); border-bottom: 1px solid rgba(0,0,0,0.08);
}
.siparisSilModal p {
    width:calc(100% - 30px); padding: 8px 15px; margin:0; font-size:13.5px; color:#555;
}
.siparisSilModal button {
    float:right; margin:15px 15px 15px 0;
}


.MuiButton-label { text-transform: none; }

::-webkit-scrollbar{width:4px;height:4px;}
::-webkit-scrollbar-button{width:0px;height:0px;}
::-webkit-scrollbar-thumb{background:#303F9F;border:0px none #000;}
::-webkit-scrollbar-thumb:hover{background:#000;}
::-webkit-scrollbar-thumb:active{background:#000000;}
::-webkit-scrollbar-track{background:#f4f4f4;border:0px none #000;}
::-webkit-scrollbar-track:hover{background:#f4f4f4;}
::-webkit-scrollbar-track:active{background:#f4f4f4;}
::-webkit-scrollbar-corner{background:transparent;}